.navbar {
    height: 80px;
    background-color: #1f223d;
    box-shadow: 0 1px 10px #1f223d, inset 0 0 5px 2px #262d6d;

    .wrapper {
        max-width: var(--max-width-desktop);
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 100%;

        span {
            font-weight: bold;
        }

        .social {
            display: flex;
            gap: 20px;
        }
    }
}
